.info-container {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.info-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  margin-bottom: 24px;
  padding: 24px;
  color: #fff;
}

.info-subscription > p {
  font-family: 'Jost', sans-serif;
}

.info-heading {
  margin-bottom: 24px;
  font-size: 40px;
  font-family: 'Jost', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.4em;
}

.info-text {
  font-family: 'Jost', sans-serif;
  padding: 10px 100px;
  margin-bottom: 24px;
  font-size: 24px;
  letter-spacing: 0.04rem;
}
