:root {
  --primary: #f8f9fa;
  --dark: #010606;
  --accent: #514d3f;
  --hover: #6b4748;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background-color: var(--accent);
  color: var(--primary);
  border: 1px solid var(--accent);
}

.btn--outline {
  background-color: transparent;
  color: var(--primary);
  padding: 8px 20px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: var(--hover);
  color: var(--white);
  transition: 250ms;
  border: solid 1px var(--hover);
}
